<template>
  <div class="home">
    <div class="home-head clearfix">
      <div class="icon-ye ye-left fl">
        <img src="../../assets/images/ye2.png" alt="" class="auto-img" />
      </div>
      <div class="home-search fl" @click="goSearch">
        <van-search
          v-model="value"
          placeholder="搜你想看的漫画"
          input-align="center"
          shape="round"
          clearable
        />
        <div class="search-icon">
          <img src="../../assets/images/12.png" alt class="auto-img" />
        </div>
      </div>
      <div class="ye-to ye-right fr">
        <img src="../../assets/images/ye3.png" alt="" class="auto-img" />
      </div>
    </div>
    <van-tabs v-model="active" title-active-color="#016831" color="#016831">
      <van-tab title="首页">
        <div class="banner">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item>
              <img src="../../assets/images/lunbo1.png" alt class="auto-img" />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../../assets/images/lunbo2.png" alt class="auto-img" />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../../assets/images/lunbo3.png" alt class="auto-img" />
            </van-swipe-item>
            <van-swipe-item>
              <img src="../../assets/images/lunbo4.png" alt class="auto-img" />
            </van-swipe-item>
          </van-swipe>
        </div>
        <div class="line">
          <div class="linebg"></div>
        </div>
        <div class="splendid">
          <div class="splendid-left">
            <div class="jingtui">精品推荐</div>
            <div class="litui">精品力推，不可错过</div>
            <div class="bucuo">
              <div class="jingbu-img">
                <div
                  class="jinalai"
                  @click="goMore('shaonianrexue', '精品推荐')"
                >
                  <img
                    src="https://ss3.bdstatic.com/70cFv8Sh_Q1YnxGkpoWK1HF6hhy/it/u=4235605524,2454243859&fm=26&gp=0.jpg"
                    alt=""
                    class="auto-img img-h"
                  />
                </div>
              </div>
            </div>
          </div>
          <div class="splendid-right">
            <div class="shudan clearfix">
              <div class="remen fl">
                <div class="remen-title">热门漫单</div>
                <div class="remen-small-title">海量漫画，免费观看</div>
              </div>
              <div
                class="remen-img fr"
                @click="goMore('wuxiagedou', '热门漫单')"
              >
                <img
                  src="https://ss1.bdstatic.com/70cFvXSh_Q1YnxGkpoWK1HF6hhy/it/u=2785571659,2049944752&fm=26&gp=0.jpg"
                  alt=""
                  class="auto-img fr-h"
                />
              </div>
            </div>
            <div class="shudan clearfix">
              <div class="remen fl">
                <div class="remen-title">书友推荐</div>
                <div class="remen-small-title">诚挚推荐，相互交流</div>
              </div>
              <div
                class="remen-img fr"
                @click="goMore('danmeirensheng', '书友推荐')"
              >
                <img
                  src="https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=712659511,3968675918&fm=26&gp=0.jpg"
                  alt=""
                  class="auto-img fr-h"
                />
              </div>
            </div>
          </div>
        </div>
        <div class="line">
          <div class="linebg"></div>
        </div>
        <div class="home-info">
          <div class="info-head clearfix">
            <div class="short fl"></div>
            <div class="info-text fl">最新更新</div>
            <div class="info-geng fr" @click="goMore('new', '最新更新')">
              更多
            </div>
          </div>
          <div class="info-img">
            <div class="book" v-for="(item, index) in renew" :key="index">
              <div class="book-img">
                <img
                  :src="item.cover"
                  alt=""
                  class="auto-img img-height"
                  @click="goLookBook(item.url)"
                />
              </div>
              <div class="book-name">{{ item.name }}</div>
            </div>
          </div>
        </div>
        <div class="home-info">
          <div class="info-head clearfix">
            <div class="short fl"></div>
            <div class="info-text fl">少女爱情</div>
            <div
              class="info-geng fr"
              @click="goMore('shaonvaiqing', '少女爱情')"
            >
              更多
            </div>
          </div>
          <div class="info-img">
            <div class="book" v-for="(item, index) in girl" :key="index">
              <div class="book-img">
                <img
                  :src="item.cover"
                  alt=""
                  class="auto-img img-height"
                  @click="goLookBook(item.url)"
                />
              </div>
              <div class="book-name">{{ item.name }}</div>
            </div>
          </div>
        </div>
        <div class="home-info">
          <div class="info-head clearfix">
            <div class="short fl"></div>
            <div class="info-text fl">恐怖灵异</div>
            <div
              class="info-geng fr"
              @click="goMore('kongbulingyi', '恐怖灵异')"
            >
              更多
            </div>
          </div>
          <div class="info-img">
            <div class="book" v-for="(item, index) in horrible" :key="index">
              <div class="book-img">
                <img
                  :src="item.cover"
                  alt=""
                  class="auto-img img-height"
                  @click="goLookBook(item.url)"
                />
              </div>
              <div class="book-name">{{ item.name }}</div>
            </div>
          </div>
        </div>
      </van-tab>
      <van-tab title="排行">
        <el-backtop></el-backtop>
        <div class="rank" v-for="(item, index) in hot" :key="index">
          <div class="rank-list">
            <div class="rank-list-left">
              <img :src="item.cover" alt="" class="auto-img" />
            </div>
            <div class="rank-list-right">
              <div class="rank-name">{{ item.name }}</div>
              <div class="rank-author">最新：{{ item.latest }}</div>
              <div class="rank-text">
                <div class="text-time fl">{{ item.time }}</div>
                <div class="text-icon fr" @click="goDetail(item.url, '排行')">
                  <img
                    src="../../assets/images/yuedu.png"
                    alt=""
                    class="auto-img book-icon"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </van-tab>
      <van-tab title="连载">
        <el-backtop></el-backtop>
        <div class="rank" v-for="(item, index) in serialize" :key="index">
          <div class="rank-list">
            <div class="rank-list-left">
              <img :src="item.cover" alt="" class="auto-img" />
            </div>
            <div class="rank-list-right">
              <div class="rank-name">{{ item.name }}</div>
              <div class="rank-author">最新：{{ item.latest }}</div>
              <div class="rank-text">
                <div class="text-time fl">{{ item.time }}</div>
                <div class="text-icon fr" @click="goDetail(item.url, '连载')">
                  <img
                    src="../../assets/images/yuedu.png"
                    alt=""
                    class="auto-img book-icon"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </van-tab>
      <van-tab title="完结">
        <el-backtop></el-backtop>
        <div class="rank" v-for="(item, index) in end" :key="index">
          <div class="rank-list">
            <div class="rank-list-left">
              <img :src="item.cover" alt="" class="auto-img" />
            </div>
            <div class="rank-list-right">
              <div class="rank-name">{{ item.name }}</div>
              <div class="rank-author">最新：{{ item.latest }}</div>
              <div class="rank-text">
                <div class="text-time fl">{{ item.time }}</div>
                <div class="text-icon fr" @click="goDetail(item.url, '完结')">
                  <img
                    src="../../assets/images/yuedu.png"
                    alt=""
                    class="auto-img book-icon"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import "../../assets/less/home.less";
export default {
  name: "Home",
  data() {
    return {
      // 默认
      active: 0,
      // 搜索
      value: "",
      // 排行
      hot: "",
      // 连载
      serialize: "",
      // 完结
      end: "",
      //参数数据列表
      showList: "",
      // 最新列表
      renew: "",
      // 少女爱情列表
      girl: "",
      // 恐怖灵异
      horrible: "",
    };
  },
  created() {
    // 排行
    this.hotShow();

    // 连载
    this.serializeShow();

    // 完结
    this.endShow();

    // 最新更新首页展示
    this.renewShow();

    // 少女爱情首页展示
    this.girlShow();

    // 恐怖灵异首页展示
    this.horribleShow();
  },

  methods: {
    //排行
    hotShow() {
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: "hot",
        },
      })
        .then((res) => {
          let resList = res.data.list;
          resList.shift();
          this.hot = resList;
        })
        .catch(() => {});
    },

    //连载
    serializeShow() {
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: "lianzai",
        },
      })
        .then((res) => {
          let resList = res.data.list;
          resList.shift();
          this.serialize = resList;
        })
        .catch(() => {});
    },

    //完结
    endShow() {
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: "wanjie",
        },
      })
        .then((res) => {
          let resList = res.data.list;
          resList.shift();
          this.end = resList;
        })
        .catch(() => {});
    },

    //排行,连载,完结查看详情
    goDetail(url, name) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({
          name: "Detail",
          query: { bookurl: url, title: name },
        });
      }, 2000);
    },

    // 最新更新首页展示
    renewShow() {
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: "new",
        },
      })
        .then((res) => {
          let resList = res.data.list;
          resList.shift();
          this.renew = resList.splice(0, 3);
        })
        .catch(() => {});
    },

    // 少女爱情首页展示
    girlShow() {
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: "shaonvaiqing",
        },
      })
        .then((res) => {
          let resList = res.data.list;
          resList.shift();
          this.girl = resList.splice(0, 3);
        })
        .catch(() => {});
    },

    // 恐怖灵异首页展示
    horribleShow() {
      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhlb: "kongbulingyi",
        },
      })
        .then((res) => {
          let resList = res.data.list;
          resList.shift();
          this.horrible = resList.splice(0, 3);
        })
        .catch(() => {});
    },

    // 精品推荐，热门漫单，书友推荐，最新更新，少女爱情，恐怖灵异
    goMore(mhlb, title) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });

      setTimeout(() => {
        this.$router.push({
          name: "More",
          query: { mhlb, title },
        });
      }, 1000);
    },

    //三书
    goLookBook(url) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      setTimeout(() => {
        this.$toast.clear();
        this.$router.push({
          name: "Detail",
          query: { bookurl: url },
        });
      }, 2000);
    },

    //搜索
    goSearch() {
      this.$router.push({ name: "Search" });
    },
  },
};
</script>

<style lang="less" scoped>
</style>